<template>
  <div class="" style="padding-top: 65px">
    <CoolLightBox
      :items="images"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>

    <div class="content mx-0 w-100 row" style="">
      <div
        class="col-6 px-1"
        v-for="product in products.results"
        :key="product.id"
      >
        <div class="product-card">
          <img
            v-lazy="product.image"
            class="item-img"
            @click="viewImage(product.image)"
            alt="Image"
            style="width: 100%"
          />
          <p class="mt-2">{{ product.barcode }}</p>
          <div class="flex-x-between">
            <!-- <p class="instock">{{ product.number_of_stock }} Instock</p> -->
            <p class="price">{{ product.price }}Ks</p>
          </div>
        </div>
      </div>
    </div>

    <div class="content w-100 mx-0 row" v-if="gettingSale">
      <div class="col-6 px-1" v-for="index in 6" :key="index">
        <div class="product-card">
          <img
            :src="isDark ? '/dark_loader.svg' : '/loader.svg'"
            class="item-img"
            alt="Image"
            style="width: 100%"
          />
        </div>
      </div>
    </div>
    <div class="flex-center" v-if="!filters.page">
      <p class="my-4 gray">End of Page</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: {
    isDark: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      products: {
        current_page: 1,
        results: [],
        total_pages: 0,
        next: 0,
      },
      filters: {
        pageSize: 10,
        page: 1,
      },
      loading_: null,
      images: [],
      index: null,
      openImage: false,
      gettingSale: true,
      hasScrolledToBottom: false,
    };
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
    this.getOrder();
  },
  watch: {
    hasScrolledToBottom(val) {
      if (val) {
        this.getOrder();
      }
    },
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll(el) {
      if (
        el.srcElement.scrollingElement.scrollHeight -
          el.srcElement.scrollingElement.clientHeight -
          200 <
          el.srcElement.scrollingElement.scrollTop &&
        !this.gettingSale
      ) {
        this.hasScrolledToBottom = true;
      }
    },

    viewImage(img) {
      this.images = [img];
      this.index = 0;
    },
    getOrder() {
      if (this.filters.page) {
        this.gettingSale = true;
        var link = `https://osassist.pythonanywhere.com/api/products/public/?store_id=2&page=${this.filters.page}&page_size=${this.filters.pageSize}`;
        axios
          .get(link)
          .then((response) => {
            response.data.results.forEach((product) => {
              if (product.number_of_stock !== 0) {
                this.products.results.push(product);
              }
            });
            this.hasScrolledToBottom = false;
            this.products.total_pages = response.data.total_pages;
            this.products.current_page = response.data.current_page;
            this.filters.page = response.data.next;
            response.data.results.forEach((item) => {
              var img = new Image();
              img.src = item.image;
            });
            this.gettingSale = false;
            const loader = document.querySelector(".page-loader");
            loader.className += " hidden";
            setTimeout(() => {
              loader.className += " display_none";
            }, 1000);
          })
          .catch(() => {
            alert("Error!");
          });
      }
    },
  },
};
</script>

    <style >
.gray {
  color: gray;
}
.price {
  color: rgb(var(--primary));
}
p.instock {
  color: gray;
  font-size: 15px !important;
}
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10000;
}
.item-img {
  height: 165px !important;
}
</style>